.nav {
  text-transform: uppercase;
  font-family: "Roboto", sans-serif; }
  .nav__list {
    display: flex;
    justify-content: space-between; }
  .nav__item {
    letter-spacing: 1px;
    padding: 8px 24px;
    margin-right: -24px;
    list-style: none; }
    .nav__item--active {
      background-color: rgba(255, 255, 255, 0.1);
      color: white;
      border-radius: 8px;
      margin-left: 0; }
  .nav__link {
    text-decoration: none;
    color: white;
    transition: color 0.3s; }
    .nav__link:hover {
      color: #9fadc2; }

